#menu {
  position: fixed;
  width: 100%;
  height: 60px;
  opacity: 0.85;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  z-index: 9999;
}
.menu-color-0 {
  background-color: #000;
  color: #FFF;
}
.menu-color-1 {
  background-color: #F5F5F5;
  color: #696969;
}
.menu-text {
  display: table;
  font-family: 'Lobster', cursive;
  float: right;
  font-size: 24px;
  height: 100%;
  margin-left: 40px;
  margin-right: auto;
}
.menu-text:hover {
    cursor: pointer;
}
.menu-text-span {
  display: table-cell;
  vertical-align: middle;
  padding-right: 20px;
}
svg {
    margin-right: 5px;
}
#header {
  background-size: 100%;
  background-repeat: no-repeat, no-repeat;
  background-size: cover;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-align: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.title-text {
  font-family: 'Lobster', cursive;
  color: #fff;
  line-height: 1em;
}
#menu:after {
  content: "";
  clear: both;
  display: block;
}
@media (min-width: 320px) {
#header { height: 280px;
}
.title { font-size: 34px;
}
}
@media (min-width: 800px) {
#header { height: 360px;
}
.title { font-size: 48px;
}
}
@media (min-width: 1200px) {
#header { height: 460px;
}
.title { font-size: 84px;
}
}
h1, h2, h3, h4, h5 {
  margin-top: 0;
  line-height: 1.2;
  color: inherit;
}
a {
  text-decoration: none;
}
header a:hover {
  text-decoration: underline;
}


main {
  margin-top: 80px;
}
article, aside, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}
.contents-box {
  width: 100%;
  margin-top: 1em;
}
.contents-inner {
  width: 70%;
  margin: 0 auto;
}
.contents-title {
  width: 100%;
  margin-top: 100px;
  text-align: center;
  font-family: 'Oswald', sans-serif;
  font-size: 42px;
}
.text {
  font-family: 'Kosugi Maru', sans-serif;
  font-size: 120%;
  line-height: 1.75;
}
.content-work-canvas {
  position: relative;
  margin-left: 15px;
}
figure {
  float: left;
  width: 45%;
  margin-bottom: 10px;
  padding: 5px 10px 5px 10px;
  display: -webkit-box;
  display: flex;
  -ms-flex-direction: column;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
          flex-direction: column;
  display: -ms-flexbox;
  word-wrap: break-word;
  background-color: #fff;
  background-clip: border-box;
  border: 1px solid rgba(0,0,0,.125);
  overflow: hidden;
  border-radius: .25rem;
}
@media (min-width: 320px) {
figure { margin-left: auto; width: 100%;
}
}
@media (min-width: 800px) {
figure { margin-left: 5px; width: 45%;
}
}
.work-image {
  height: 300px;
}
.caption {
  position: relative;
}
@media (min-width: 320px) {
.caption { height: 190px;
}
}
@media (min-width: 960px) {
.caption { height: 200px;
}
}
@media (min-width: 1200px) {
.caption { height: 160px;
}
}
.overview {
  font-family: 'Kosugi Maru', sans-serif;
  margin-top: 10px;
}
.bold {
  font-weight: bold;
}
#my-works:after, #my-skills:after {
  content: "";
  clear: both;
  display: block;
}
.env-box {
  position: absolute;
  bottom: 2px;
}
.environment-m, .environment-l {
  font-family: 'Roboto Condensed', sans-serif;
  background-color: #F5F5F5;
  display: inline-block;
  margin-top: 2px;
  font-size: 14px;
  text-align: center;
  line-height: 1.75em;
  border: 1px solid #888888;
  border-radius: 12px 12px 12px 12px;
}
.environment-m {
  width: 64px;
}
.environment-l {
  width: 76px;
}
.contents-inner-skill {
  max-width: 87%;
  margin: 0 auto;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.skill-canvas {
  position: relative;
  margin: 5px;
  width: 31%;
  height: 40px;
  border: 2px solid #f0f0f0;
  border-radius: 10px 10px 10px 10px;
  background-color: #FFF;
  display: inline-block;
}
@media (min-width: 320px) {
.skill-canvas { width: 90%;
}
}
@media (min-width: 960px) {
.skill-canvas { width: 45%;
}
}
@media (min-width: 1200px) {
.skill-canvas { width: 31%;
}
}
.skill {
  font-family: 'Oswald', sans-serif;
  font-size: 100%;
  text-align: center;
  line-height: 1em;
  display: table;
  width: 100%;
}
.skill li {
  position: relative;
  display: table-cell;
  width: 55%;
  line-height: 2.5em;
}



#footer {
  margin-top: 100px;
  background-color: #000;
  height: 260px;
  width: 100%;
}
